import { ChakraProvider } from '@chakra-ui/react';
// import Header from './components/header';
import MainBody from './components/Mainbody';
import { Box, Button, Heading, Input} from '@chakra-ui/react';
import { useEffect, useState } from 'react';
import axios from 'axios';

function App() {
    const fetchVideos = async (currentPage) => {
        const result = await axios.get(
            `http://localhost:5000/api/v1/videos/get/${
                currentPage ? currentPage : 1
            }?limit=12`
        );
        setVideos(result.data.data);
        setCurrentPage(result.data.currentPage);
        SetTotalPage(result.data.totalPages);
    };
    const SearchVideos = async (searchString) => {
        const result = await axios.get(
            `http://localhost:5000/api/v1/videos/search/${searchString}`
        );
        setVideos(result.data.data);
        setCurrentPage(result.data.currentPage);
        SetTotalPage(result.data.totalPages);
        if(result.data.data.length<1){
            setShowText('No video exist with that search query.')
        }
    };

    const handleSearch = async () => {
        SearchVideos(searchValue);
        setSearchValue('');
    };
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, SetTotalPage] = useState(2);
    const [videos, setVideos] = useState([]);
    const [isPreviousDisabled, setPreviousIsDisabled] = useState(false);
    const [isNextDisabled, setNextIsDisabled] = useState(false);
    const [searchValue, setSearchValue] = useState('');
    const [showText,setShowText] = useState('Loading....')


    useEffect(() => {
        fetchVideos(currentPage);
        if (currentPage > 1 && currentPage < totalPages) {
            setNextIsDisabled(false);
            setPreviousIsDisabled(false);
        }
        if (currentPage >= totalPages) {
            setNextIsDisabled(true);
        }
        if (currentPage <= 1) {
            setPreviousIsDisabled(true);
        }
    }, [currentPage]);

    const handlePrevious = () => {
        if (currentPage > 1) setCurrentPage(currentPage - 1);
    };

    const handleNext = () => {
        if (currentPage <= totalPages) setCurrentPage(currentPage + 1);
    };

    return (
        <ChakraProvider>
            {/* ---------------------------------------Header------------------------------------------------------------*/}
            <Box
                width="100%"
                h="5rem"
                bg="#1d3557"
                display="flex"
                alignItems="center"
                justifyContent="space-around"
            >
                    <Heading as="h3" size="2xl" color="#f1faee">
                        Youtube videos
                    </Heading>
                <Box
                    width="50%"
                    display="flex"
                    alignItems="center"
                    justifyContent="space-around"
                >
                    <Input
                        value={searchValue}
                        onChange={(e) => {
                            setSearchValue(e.target.value);
                        }}
                        variant="outline"
                        placeholder="Search"
                        width="60%"
                        bg="white"
                        border="2px"
                        borderColor="red.200"
                    />
                    <Button
                        colorScheme="teal"
                        size="md"
                        mr="5rem"
                        onClick={handleSearch}
                    >
                        Search
                    </Button>
                </Box>
            </Box>
            {/* --------------------------------------Main Body-----------------------------------------------------------*/}
            {videos.length>1?<MainBody videos={videos}></MainBody>:<Box width="100%"
            height="100%" display="flex" alignContent="center" alignItems="center" m="10rem">
                     <Heading as="h2" size="2xl" color="##1d3557">
                       {showText}
                    </Heading>
                </Box>}
            {/* ---------------------------------------Footer-------------------------------------------------------------*/}
            <Box
                width="100%"
                h="5rem"
                bg="#1d3557"
                display="flex"
                alignItems="center"
                justifyContent="space-around"
            >
                <Button
                    colorScheme="teal"
                    size="md"
                    ml="30rem"
                    onClick={handlePrevious}
                    isDisabled={isPreviousDisabled}
                >
                    Previous
                </Button>
                <Button
                    colorScheme="teal"
                    size="md"
                    mr="30rem"
                    onClick={handleNext}
                    isDisabled={isNextDisabled}
                >
                    Next
                </Button>
            </Box>
        </ChakraProvider>
    );
}

export default App;
