import { Box, Grid, GridItem } from '@chakra-ui/react';
import React from 'react';
import Card from './card';

const MainBody = ({ videos }) => {
    const allVideos = videos.map((video, key) => {
        return (
            <GridItem w="100%" h="13rem" bg="gray.200" key={key}>
                <Card
                    image={video.thumbnails.medium.url}
                    title={video.title}
                    channelName={video.channelTitle}
                    videoId={video.videoId}
                    description={video.description}
                ></Card>
            </GridItem>
        );
    });
    return (
        <Box
            width="100%"
            h="100%"
            bg="gray.100"
            display="flex"
            flexDirection="column"
            id="Main card"
        >
            <Grid templateColumns="repeat(4, 1fr)" gap={9} padding="2rem">
                {allVideos}
            </Grid>
        </Box>
    );
};
export default MainBody;
